    :depto="mcsframe.depto"
    :days="mcsframe.dias_laborados"
    :m3="mcsframe.m3_persona"
    :bonus_p="mcsframe.bono_persona"
    :bonus_tot="mcsframe.bono_depto"
    :asis="mcsframe.asistencia"
    :extra="mcsframe.$_extra_m3"
    :bonus_prod="mcsframe.bono_productividad"
    :bonus_goals="mcsframe.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'McsFrame',
    components: {DeptoBase},
    created() {   
        this.$store.dispatch("plants/getInfo", {
            department: 'mcsframe',
            userCode: this.$route.params.user,
